import React, { useState, useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import { ethers } from 'ethers'
import styled from 'styled-components'
import { Modal, Box, Text, ButtonMenu, ButtonMenuItem } from 'archerswap-uikit'
import useTokenBalance from 'hooks/useTokenBalance'
import { getBalanceNumber } from 'utils/formatBalance'
import { useTranslation } from 'contexts/Localization'
import ApproveConfirmButtonsWithWhitelist from 'views/Profile/components/ApproveConfirmButtonsWithWhitelist'
import useApproveConfirmTransaction from 'hooks/useApproveConfirmTransaction'
import { useERC20, useHunter, useXbow } from 'hooks/useContract'
import BalanceInput from './BalanceInput'

const ToggleWrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;

  ${({ theme }) => theme.mediaQueries.sm} {
    margin-left: 16px;
  }
`

interface Props {
  privateSale: boolean
  currency: string
  contract: any
  currencyAddress: string
  currencyDecimals: number
  contributedBalance?: number
  maxDepositAmount?: number
  onSuccess: (amount: BigNumber) => void
  onDismiss?: () => void
}

const ContributeModal: React.FC<Props> = ({
  privateSale,
  currency,
  contract,
  currencyAddress,
  currencyDecimals,
  contributedBalance,
  maxDepositAmount,
  onDismiss,
  onSuccess,
}) => {
  const [value, setValue] = useState('')
  const { account } = useWeb3React()
  const raisingTokenContract = useERC20(currencyAddress)
  const balance = getBalanceNumber(useTokenBalance(currencyAddress), currencyDecimals)
  const { t } = useTranslation()
  const valueWithTokenDecimals = new BigNumber(value).times(new BigNumber(10).pow(currencyDecimals))

  const [refresh, setRefresh] = useState(0)

  const {
    isWhitelisted,
    isApproving,
    isApproved,
    isConfirmed,
    isConfirming,
    handleApprove,
    handleConfirm,
  } = useApproveConfirmTransaction({
    onWhitelisted: useCallback(async () => {
      try {
        const response = await contract.methods.isWhitelisted(account).call()
        return response
      } catch (error) {
        return false
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [refresh, account, contract]),
    onRequiresApproval: useCallback(async () => {
      try {
        const response = await raisingTokenContract.methods.allowance(account, contract.options.address).call()
        const currentAllowance = new BigNumber(response)
        return currentAllowance.gt(0)
      } catch (error) {
        return false
      }
    }, [raisingTokenContract, account, contract]),
    onApprove: () => {
      return raisingTokenContract.methods
        .approve(contract.options.address, ethers.constants.MaxUint256)
        .send({ from: account })
    },
    onConfirm: () => {
      return contract.methods.deposit(valueWithTokenDecimals.toString()).send({ from: account })
    },
    onSuccess: async () => {
      onDismiss()
      onSuccess(valueWithTokenDecimals)
    },
  })

  const [isHunter, setIsHunter] = useState(true)

  const hunterContract = useHunter()
  const xBowContract = useXbow()
  const isWhitelistedHunter = true

  const {
    isApproving: isApprovingHunter,
    isApproved: isApprovedHunter,
    isConfirmed: isConfirmedHunter,
    isConfirming: isConfirmingHunter,
    handleApprove: handleApproveHunter,
    handleConfirm: handleConfirmHunter,
  } = useApproveConfirmTransaction({
    onRequiresApproval: useCallback(async () => {
      try {
        const response = isHunter
          ? await hunterContract.methods.allowance(account, contract.options.address).call()
          : await xBowContract.methods.allowance(account, contract.options.address).call()
        const currentAllowance = new BigNumber(response)
        return currentAllowance.gt(0)
      } catch (error) {
        return false
      }
    }, [isHunter, account, contract, hunterContract, xBowContract]),
    onApprove: () => {
      return isHunter
        ? hunterContract.methods.approve(contract.options.address, ethers.constants.MaxUint256).send({ from: account })
        : xBowContract.methods.approve(contract.options.address, ethers.constants.MaxUint256).send({ from: account })
    },
    onConfirm: () => {
      return contract.methods.lock(isHunter).send({ from: account })
    },
    onSuccess: async () => {
      setRefresh(refresh + 1)
    },
  })

  const handleMax = () => {
    let temp = balance
    if (maxDepositAmount > 0 && maxDepositAmount - contributedBalance < temp) {
      temp = maxDepositAmount - contributedBalance
    }

    setValue(temp.toString())
  }

  return (
    <Modal title={t('Contribute %symbol%', { symbol: currency })} onDismiss={onDismiss}>
      <Box maxWidth="400px">
        {(!privateSale || isWhitelisted) && (
          <BalanceInput
            title={t('Contribute')}
            value={value}
            onChange={(e) => {
              if (parseFloat(e.currentTarget.value) >= 0 || !e.currentTarget.value) setValue(e.currentTarget.value)
            }}
            symbol={currency}
            max={balance}
            onSelectMax={() => handleMax()}
            mb="24px"
          />
        )}
        {!privateSale || isWhitelisted ? (
          <Text as="p" mb="24px">
            {t(
              "If you don't contribute enough BOW tokens, you may not receive any IDO tokens at all and will only receive a full refund of your BOW tokens.",
            )}
          </Text>
        ) : (
          <Text as="p" mb="24px">
            {t('To get whitelisted, you need to lock 200 xBOW (300 HUNT) till the sale ends.')}
          </Text>
        )}
        {privateSale && !isWhitelisted && (
          <>
            <ToggleWrapper>
              <ButtonMenu
                activeIndex={isHunter ? 0 : 1}
                onItemClick={(index) => {
                  setIsHunter(index === 0)
                }}
                scale="sm"
                variant="primary"
              >
                <ButtonMenuItem style={{ borderRadius: '30px', width: '140px' }}>{t('Hunter')}</ButtonMenuItem>
                <ButtonMenuItem style={{ borderRadius: '30px', width: '140px' }}>{t('xBow')}</ButtonMenuItem>
              </ButtonMenu>
            </ToggleWrapper>
            <ApproveConfirmButtonsWithWhitelist
              privateSale={privateSale}
              isWhitelisted={isWhitelistedHunter}
              isApproveDisabled={isConfirmedHunter || isConfirmingHunter || isApprovedHunter}
              isApproving={isApprovingHunter}
              isConfirmDisabled={!isApprovedHunter || isConfirmedHunter}
              isConfirming={isConfirmingHunter}
              onApprove={handleApproveHunter}
              onConfirm={handleConfirmHunter}
            />
          </>
        )}
        {(!privateSale || isWhitelisted) && (
          <ApproveConfirmButtonsWithWhitelist
            privateSale={privateSale}
            isWhitelisted={isWhitelisted}
            isApproveDisabled={isConfirmed || isConfirming || isApproved}
            isApproving={isApproving}
            isConfirmDisabled={
              !isApproved ||
              isConfirmed ||
              valueWithTokenDecimals.isNaN() ||
              valueWithTokenDecimals.eq(0) ||
              valueWithTokenDecimals.gt(new BigNumber(balance).times(new BigNumber(10).pow(currencyDecimals))) ||
              (maxDepositAmount > 0 &&
                valueWithTokenDecimals.gt(
                  new BigNumber(maxDepositAmount - contributedBalance).times(new BigNumber(10).pow(currencyDecimals)),
                ))
            }
            isConfirming={isConfirming}
            onApprove={handleApprove}
            onConfirm={handleConfirm}
          />
        )}
        {/* <LinkExternal
          href={`/add/CORE/${currencyAddress}`}
          style={{ margin: '16px auto 0' }}
        >
          {`Get ${currency}`}
        </LinkExternal> */}
      </Box>
    </Modal>
  )
}

export default ContributeModal
