import React from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { Contract } from 'web3-eth-contract'
import { AutoRenewIcon, Box, Button, Flex, Text, HelpIcon } from 'archerswap-uikit'
import moment from 'moment'
import { useToast } from 'state/hooks'
import { useTranslation } from 'contexts/Localization'
import { getBalanceNumber } from 'utils/formatBalance'
import { Ifo } from 'config/constants/types'
import ToolTip from './Tooltip'
import { UserInfo, UserVestingInfo, WalletIfoState } from '../../hooks/useGetWalletIfoData'
import { PublicIfoState } from '../../hooks/useGetPublicIfoData'
import BalanceInUsd from './BalanceInUsd'
import MetaLabel from './MetaLabel'

interface ClaimProps {
  ifo: Ifo
  contract: Contract
  userInfo: UserInfo
  userVestingInfo: UserVestingInfo
  isPendingTx: WalletIfoState['isPendingTx']
  setPendingTx: (status: boolean) => void
  offeringTokenBalance: WalletIfoState['offeringTokenBalance']
  refundingAmount: WalletIfoState['refundingAmount']
  setIsClaimed: () => void
  publicIfoData: PublicIfoState
}

const AmountGrid = styled.div`
  display: grid;
  grid-gap: 8px;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 24px;
`

const DISPLAY_DECIMALS = 4

const Claim: React.FC<ClaimProps> = ({
  ifo,
  contract,
  userInfo,
  userVestingInfo,
  isPendingTx,
  setPendingTx,
  offeringTokenBalance,
  refundingAmount,
  setIsClaimed,
  publicIfoData,
}) => {
  const { t } = useTranslation()
  const { account } = useWeb3React()
  const didContribute = userInfo.amount.gt(0)
  const canClaim = !userInfo.claimed || userVestingInfo.releasable.gt(0)
  const { tokenSymbol, tokenDecimals, currencyDecimals, vestingPercentage } = ifo
  const contributedBalance = getBalanceNumber(userInfo.amount, currencyDecimals)
  const refundedBalance = getBalanceNumber(refundingAmount, currencyDecimals).toFixed(
    userInfo.amount.eq(0) ? 0 : DISPLAY_DECIMALS,
  )
  const rewardBalance = getBalanceNumber(offeringTokenBalance, tokenDecimals)
  const { toastError, toastSuccess } = useToast()
  const { liquidityIsCreated, vestingDuration, vestingSlicePeriodSeconds, endTimestamp } = publicIfoData

  const handleClaim = async () => {
    try {
      setPendingTx(true)
      await contract.methods.harvest().send({ from: account })
      setIsClaimed()
      toastSuccess('Success!', 'You have successfully claimed your rewards.')
    } catch (error) {
      toastError('Error', error?.message)
      console.error(error)
    } finally {
      setPendingTx(false)
    }
  }

  const handleRelease = async () => {
    try {
      setPendingTx(true)
      await contract.methods.release().send({ from: account })
      setIsClaimed()
      toastSuccess('Success!', 'You have successfully claimed your rewards.')
    } catch (error) {
      toastError('Error', error?.message)
      console.error(error)
    } finally {
      setPendingTx(false)
    }
  }

  const buttonLabel = () => {
    if (!liquidityIsCreated) {
      return t('Adding Liquidity...')
    }
    if (canClaim) {
      if (!vestingPercentage) {
        return t('Claim')
      }

      let claimable = getBalanceNumber(offeringTokenBalance.times((100 - vestingPercentage) / 100), tokenDecimals)
      if (userVestingInfo.releasable.gt(0)) {
        claimable = getBalanceNumber(userVestingInfo.releasable, tokenDecimals)
      }
      return `${t('Claim')}(${claimable})`
    }

    return t('Claimed')
  }

  const vestingText = () => {
    const lastReleaseTimeEst = userVestingInfo.released.gt(0)
      ? Math.floor(userVestingInfo.amountTotal.times(vestingDuration).div(userVestingInfo.released).toNumber())
      : 0

    return (
      <>
        <span>{`${t('Initial release')}:  ${getBalanceNumber(
          offeringTokenBalance.times((100 - vestingPercentage) / 100),
          tokenDecimals,
        )} ${tokenSymbol}`}</span>
        <br />
        <span>{`${t('Total vesting')}: ${getBalanceNumber(
          userVestingInfo.amountTotal,
          tokenDecimals,
        )} ${tokenSymbol}`}</span>
        <br />
        <span>{`${t('Vesting claimed')}: ${getBalanceNumber(
          userVestingInfo.released,
          tokenDecimals,
        )} ${tokenSymbol}`}</span>
        <br />
        {lastReleaseTimeEst > 0 && (
          <>
            <span>{`${t('Claimable time')}: ${moment
              .unix(endTimestamp + lastReleaseTimeEst + vestingSlicePeriodSeconds)
              .format('YYYY-MM-DD hh:mm A')}`}</span>
            <br />
          </>
        )}
      </>
    )
  }

  return (
    <>
      <AmountGrid>
        <Box>
          <Flex mb="4px">
            <Text as="span" bold fontSize="12px" mr="4px" textTransform="uppercase">
              {ifo.currency} {t('Tokens')}
            </Text>
            <Text as="span" color="textSubtle" fontSize="12px" textTransform="uppercase" bold>
              {t('Committed')}
            </Text>
          </Flex>
          <Text fontSize="20px" bold color={offeringTokenBalance.gt(0) ? 'text' : 'textDisabled'}>
            {contributedBalance.toFixed(userInfo.amount.eq(0) ? 0 : DISPLAY_DECIMALS)}
          </Text>
          <MetaLabel>{canClaim ? `${refundedBalance} to reclaim` : `${refundedBalance} reclaimed`}</MetaLabel>
        </Box>
        <Box>
          <Flex mb="4px">
            <Text as="span" bold fontSize="12px" mr="4px" textTransform="uppercase">
              {tokenSymbol}
            </Text>
            {!canClaim ? (
              <Text as="span" color="textSubtle" fontSize="12px" textTransform="uppercase" bold>
                Claimed
              </Text>
            ) : (
              <Text as="span" color="textSubtle" fontSize="12px" textTransform="uppercase" bold>
                To Claim
              </Text>
            )}
            {vestingPercentage > 0 && (
              <ToolTip content={vestingText()}>
                <HelpIcon color="textSubtle" style={{ marginLeft: '5px' }} />
              </ToolTip>
            )}
          </Flex>
          <Text fontSize="20px" bold color={offeringTokenBalance.gt(0) ? 'text' : 'textDisabled'}>
            {rewardBalance.toFixed(offeringTokenBalance.eq(0) ? 0 : DISPLAY_DECIMALS)}
          </Text>
          {canClaim && <BalanceInUsd token={tokenSymbol} balance={rewardBalance} />}
        </Box>
      </AmountGrid>
      {didContribute ? (
        <Button
          onClick={!userInfo.claimed ? handleClaim : handleRelease}
          disabled={isPendingTx || !canClaim || !liquidityIsCreated}
          width="100%"
          mb="24px"
          isLoading={isPendingTx}
          endIcon={isPendingTx ? <AutoRenewIcon spin color="currentColor" /> : null}
        >
          {buttonLabel()}
        </Button>
      ) : (
        <Button disabled width="100%" mb="24px">
          {t('Nothing to Claim')}
        </Button>
      )}
      {!liquidityIsCreated ? (
        <Text mt="4px" fontSize="14px">
          {t('Please wait until liquidity is created')}
        </Text>
      ) : (
        <Text mt="4px" fontSize="14px">
          {t('Youll be refunded any excess tokens when you claim')}
        </Text>
      )}
    </>
  )
}

export default Claim
