import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import { Ifo } from 'config/constants/types'
import { useERC20, useIfoContract } from 'hooks/useContract'
import { useIfoAllowance } from 'hooks/useAllowance'
import makeBatchRequest from 'utils/makeBatchRequest'

export interface UserInfo {
  amount: BigNumber
  claimed: boolean
}

export interface UserVestingInfo {
  amountTotal: BigNumber
  released: BigNumber
  releasable: BigNumber
}

export interface WalletIfoState {
  isPendingTx: boolean
  offeringTokenBalance: BigNumber
  refundingAmount: BigNumber
  userInfo: UserInfo
  userVestingInfo: UserVestingInfo
}

/**
 * Gets all data from an IFO related to a wallet
 */
const useGetWalletIfoData = (ifo: Ifo) => {
  const [state, setState] = useState<WalletIfoState>({
    isPendingTx: false,
    offeringTokenBalance: new BigNumber(0),
    refundingAmount: new BigNumber(0),
    userInfo: {
      amount: new BigNumber(0),
      claimed: false,
    },
    userVestingInfo: {
      amountTotal: new BigNumber(0),
      released: new BigNumber(0),
      releasable: new BigNumber(0),
    },
  })

  const { address, currencyAddress, vestingPercentage } = ifo
  const { isPendingTx } = state

  const { account } = useWeb3React()
  const contract = useIfoContract(address)
  const currencyContract = useERC20(currencyAddress)
  const allowance = useIfoAllowance(currencyContract, address, isPendingTx)

  const setPendingTx = (status: boolean) =>
    setState((prevState) => ({
      ...prevState,
      isPendingTx: status,
    }))

  const addUserContributedAmount = (amount: BigNumber) => {
    setState((prevState) => ({
      ...prevState,
      userInfo: {
        ...prevState.userInfo,
        amount: prevState.userInfo.amount.plus(amount),
      },
    }))
  }

  const setIsClaimed = () => {
    setState((prevState) => ({
      ...prevState,
      userInfo: {
        ...prevState.userInfo,
        claimed: true,
      },
    }))
  }

  useEffect(() => {
    const fetchIfoData = async () => {
      let offeringAmount
      let userInfoResponse
      let refundingAmount
      let userVestingInfoResponse
      let releasableAmount
      if (vestingPercentage > 0) {
        ;[
          offeringAmount,
          userInfoResponse,
          refundingAmount,
          userVestingInfoResponse,
          releasableAmount,
        ] = (await makeBatchRequest([
          contract.methods.getOfferingAmount(account).call,
          contract.methods.userInfo(account).call,
          contract.methods.getRefundingAmount(account).call,
          contract.methods.getVestingInfo(account).call,
          contract.methods.computeReleasableAmount(account).call,
        ])) as [string, UserInfo, string, any, string]
      } else {
        ;[offeringAmount, userInfoResponse, refundingAmount] = (await makeBatchRequest([
          contract.methods.getOfferingAmount(account).call,
          contract.methods.userInfo(account).call,
          contract.methods.getRefundingAmount(account).call,
        ])) as [string, UserInfo, string]
      }

      if (vestingPercentage > 0) {
        setState((prevState) => ({
          ...prevState,
          offeringTokenBalance: new BigNumber(offeringAmount),
          refundingAmount: new BigNumber(refundingAmount),
          userInfo: {
            amount: new BigNumber(userInfoResponse.amount),
            claimed: userInfoResponse.claimed,
          },
          userVestingInfo: {
            amountTotal: new BigNumber(userVestingInfoResponse.amountTotal),
            released: new BigNumber(userVestingInfoResponse.released),
            releasable: new BigNumber(releasableAmount),
          },
        }))
      } else {
        setState((prevState) => ({
          ...prevState,
          offeringTokenBalance: new BigNumber(offeringAmount),
          refundingAmount: new BigNumber(refundingAmount),
          userInfo: {
            amount: new BigNumber(userInfoResponse.amount),
            claimed: userInfoResponse.claimed,
          },
        }))
      }
    }

    if (account) {
      fetchIfoData()
    }
  }, [account, vestingPercentage, contract, setState])

  return { ...state, allowance, contract, setPendingTx, addUserContributedAmount, setIsClaimed }
}

export default useGetWalletIfoData
